import UniTag from '@/components/uni-tag/uni-tag.vue'
export default {
	components: {
		UniTag,
	},
	data() {
		return {
			curry: 0,
			constData: this.$constData,
			list: {}, //内容列表

			id: '',
			titleText: '',
			titleTime: '',
			color: '',

			tagList: [{
					name: '全部',
					type: 2
				},
				{
					name: '免费',
					type: 0
				},
				{
					name: '付费',
					type: 1
				}
			],
		}
	},
	methods: {
		//跳转支付页
		navToPay(){
			uni.navigateTo({
				url: `/pages/vip/column/payView/payView?id=${this.id}`
			})
		},
		//跳转详情
		navigator(list) {
			if (list.paid == this.$constData.contentPaid[1].key) {
				uni.showToast({
					title: '购买后可观看',
					duration: 1500,
					icon: 'none'
				});
			}
			let url = ''
			if (list.type == this.$constData.contentType[0].key || list.type == this.$constData.contentType[2].key) {
				url = 'details'
			} else if (list.type == this.$constData.contentType[1].key) {
				url = 'detailsVideo'
			}
			uni.navigateTo({
				url: `/pages/vip/column/${url}/${url}?id=${list.id}&id1=${list._id}`
			})
		},
		// 从专栏id获取课程列表
		getContentByChannelId() {
			let cnt = {
				module: this.$constData.module, // String 隶属
				channelId: this.id, // Long 专栏id
				status: 4, // Byte 专栏状态
				count: 10, // Integer 
				offset: 0, // Integer 
			}
			this.$api.getContentByChannelId(cnt, (res) => {
				let arr = []
				if (res.data.rc == this.$util.RC.SUCCESS) {
					this.list = JSON.parse(res.data.c).list
				} else {
					arr = []
					console.log('error')
				}
			})
		},
		getTime(e) {
			let t = parseInt(e)
			let a = new Date(t)
			let y = a.getFullYear()
			let m = 1 + a.getMonth()
			let d = a.getDate()
			let time = y + '年' + m + '月' + d + '日'
			this.titleTime = time
		}
	},
	onLoad(options) {
		console.log(options)
		this.id = options.id
		this.titleText = options.title
		this.color = options.color
		this.getTime(options.time)
		this.getContentByChannelId()
	},

}
